@font-face {
  font-family: 'Amaranth';
  src: url('../public/fonts/Amaranth-Regular.woff2') format('woff2'),
    url('../public/fonts/Amaranth-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

